<template>
  <div style="height: 100vh;">
    <JsonEditorVue ref="editor"  v-model="content.value" :parser="LosslessJSONParser" style="height: 100vh;"
      :flattenColumns="true" :onRenderMenu="RenderMenu" />
  </div>
</template>

<script setup>
import JsonEditorVue from 'json-editor-vue'
import { parse, stringify } from 'lossless-json'
import { onMounted, ref } from 'vue';
const LosslessJSONParser = {
  parse,
  stringify,
}
let content = {
    value:undefined,
    escapeControlCharacters:true,
    escapeUnicodeCharacters:true
}

const editor = ref(null)
function RenderMenu(mode, items) {
  items.pop()
  items.push({
    onClick: function (a, b) {
      content.escapeControlCharacters = !content.escapeControlCharacters
      editor.value.jsonEditor.updateProps({
        escapeControlCharacters: content.escapeControlCharacters
      })
    },
    // icon: IconDefinition
    text: "转义字符显示切换",
    title: "转义字符显示切换",
    className: "button-text",
    disabled: false
  })

  items.push({
    onClick: function (a, b) {
      content.escapeUnicodeCharacters = !content.escapeUnicodeCharacters
      editor.value.jsonEditor.updateProps({
        escapeUnicodeCharacters: content.escapeUnicodeCharacters
      })
    },
    // icon: IconDefinition
    text: "Unicode字符显示切换",
    title: "Unicode字符显示切换",
    disabled: false,
    className: "button-text"
  })
  return items
}
onMounted(() => {
  chrome.runtime.sendMessage({}, function (response){
    var json = response.clipboard;
    try {
      editor.value.jsonEditor.set({text:json})
    } catch(e) { }
  });
  
})

</script>

<style>
.button-text {
  width: auto !important
}
</style>